.hexagon-container {
  position: relative;
}

.hexagon,
.hexagon-surround {
  position: absolute;
  width: 100px; /* Adjust the size of the hexagons as needed */
  padding-bottom: 115.47%; /* This value is calculated for a regular hexagon */
  background-color: #4285f4; /* Adjust the background color as needed */
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
}

.hexagon-surround {
  transform: rotate(60deg);
}

/* Adjust positioning of surrounding hexagons as needed */
.hexagon-surround:nth-child(1) {
  top: -57.73%;
  left: 0;
}

.hexagon-surround:nth-child(2) {
  top: -28.87%;
  left: 50%;
  transform: translateX(-50%) rotate(60deg);
}
