/* testimonial css###################### */
.testimonial {
  /* margin-top: 80px; */
 width: 80%;
 margin-left:10%;
 margin-right: 10%;
}

.testimonial .col-lg-4 {
  display: flex;
  justify-content: center;
}

.test-card {
  width: 250px;
  height: 250px;
  transform-style: preserve-3d;
  perspective: 500px;
  border: none;
  background-color: inherit;
}

.test-card .face {
  position: absolute;
  color: #2c5c74;
  width: 100%;
  height: 100%;
  overflow: hidden;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.06);
  transform-style: preserve-3d;
  transition: 0.5s;
  backface-visibility: hidden;
  border-top: 1px solid #10054b;
  border-left: 1px solid #1a0647;
}

.test-card .face.front-face,
.test-card .face.back-face {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.test-card .face.front-face .profile {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.test-card .face.front-face .name {
  letter-spacing: 2px;
}

.test-card .face.front-face .designation {
  font-size: 0.8rem;
  color: #ddd;
  letter-spacing: 0.8px;
}

.test-card:hover .face.front-face {
  transform: rotateY(180deg);
}

.test-card .face.back-face {
  position: absolute;
  background: rgba(255, 255, 255, 0.06);
  transform: rotateY(180deg);
  padding: 20px 30px;
  text-align: center;
  user-select: none;
}

.test-card .face.back-face .fa-quote-left {
  position: absolute;
  top: 25px;
  left: 25px;
  font-size: 1.2rem;
}

.test-card .face.back-face .fa-quote-right {
  position: absolute;
  bottom: 35px;
  right: 25px;
  font-size: 1.2rem;
}

.test-card:hover .face.back-face {
  transform: rotateY(360deg);
}

@media(max-width: 991.5px) {
  .testimonial .col-lg-4 {
      margin-top: 40px;
      margin-bottom: 20px;
  }
}
